.quizz__container {

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  
  
  
  .quizz__puntuation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 40px;
    margin-top: 20px;
    width: 100%;
    height: 50px;

    .quizz__button {
      align-self: center;
      width: 40%;
    }

    .quizz__puntuation_number {
      display: flex;
      font-size: 2em;
      img {
        height: 50px;
        margin-left: 10px;
        width: 50px;
      }
    }
  }

  .quizz__h1 {
    font-size: 2em;
    letter-spacing: 1px;
    text-align: center;
    flex-direction: column;
  }

  .true{
    color: green;
    font-size: 4em;
  }

  .quizz__options {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 15px;
    margin: 40px 0px;
  }
  .quizz__option { 
    font-size: 2em; 
    font-weight: bold; 
    border: 2px solid rgba(255, 255, 255, 0.2);
    box-sizing: border-box;
    transition: transform 200ms;
   
    
  }

  .quizz__option_active{
   
      border: 2px solid rgba(255, 255, 255, 0.5);
      transform: scale(1.02);
    
  }
  
  .quizz__error {
    height: 100px;
    font-weight: 800;
    font-size: 1em;
    h2{
      text-align: center;
    }
  }
  
  .quizz__error_icon {
    width: 40px;
    display: block;
    margin: 0px auto;
  }

  @media (max-width: 700px) {
    .quizz__puntuation {
      margin-bottom: 20px;
    }
    
    .question__container {
      margin-top: 10px;
      margin-bottom: 0px;
    }
  }
  
  @media (min-width: 510px){
    .quizz__option{
      min-width: 500px;
      
    }
  }
}
