@import "./variables";

@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@100;200;300;400;500;600;800;900&display=swap);

@import url(https://fonts.googleapis.com/css2?family=Paytone+One&display=swap);

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  color: whitesmoke;
}

a {
  display: block;
  text-align: center;
  text-decoration: none;
  font-family: "Catamaran", sans-serif;
}

h1 {
  font-family: "Paytone One", sans-serif;
  letter-spacing: 1px;
}

body {
  background: linear-gradient(to right, #0f0c29, #302b63, #24243e);

  font-family: "Catamaran", sans-serif;
  max-height: 100vh;
}

label {
  font-family: "Paytone One", sans-serif;
  font-weight: 600;
  font-size: 1.5em;
}

.btn-primary {
  background: $button;
  border-radius: 5px;
  border: 3px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 40px;
  margin-top: 15px;
  padding: 5px 20px;
  width: 100%;
  &:hover {
    box-shadow: 2px 10px 5px -3px rgba(176, 176, 176, 0.2);
  }
}

.center {
  display: block;
  margin: 0px auto;
}

button.btn-secondary {
  padding: 6px 20px;
}


.btn-secondary {
  background: transparent;
  border-radius: 5px;
  border: 3px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
  font-size: 1.5em;
  padding: 5px 20px;
  width: 100%;
  &:hover {
    background: rgba(255, 255, 255, 0.2);
    box-shadow: 2px 10px 5px -3px rgba(176, 176, 176, 0.1);
  }
}

.pb-2 {
  margin-bottom: 20px;
}
