.gameover {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 500px;
  max-width: 500px;
  .gameover__icon-fiesta {
    //position: absolute;
    width: 250px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    opacity: 0.3;
  }
}

.gameover__exit_btn {
  display: block;
  width: 50%;
  padding-bottom: 10px;
}

.gameover__title {
  font-size: 2.5em;
  text-align: center;
  margin-bottom: 10px;
}

.gameover__icon {
  display: block;
  margin: 0px auto;
  width: 150px;
}

.gameover__points {
  font-size: 2.5em;
  text-align: center;
}

.gameover__best {
  text-align: center;
}

.gameover__btn {
  display: block;
  width: 100%;
  margin-top: 25px;
  text-align: center;
}

@media (max-width: 700px) {
  .gameover {
    max-width: auto;
    width: 100%;
  }
}
